@use "bootstrap" as v;

.atcb {
	display: none;
}

.atcb_button_wrapper {
	display: inline-block;
	position: relative;
}

.atcb_button {
	background: #f5f5f5;
	background: var(--c-primary);
	border: 1px solid #d2d2d2;
	border: none;
	border-radius: 0;
	// -webkit-box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, .4);
	// box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, .4);
	// box-shadow: var(--box-shadow);
	color: #fff;
	cursor: pointer;
	display: inline-block;
	font-size: 16px;
	font-weight: 600;
	line-height: 24px;
	margin: 10px auto;
	max-width: 300px;
	min-width: 150px;
	padding: 10px 16px 11px 16px;
	position: relative;
	text-align: center;
	touch-action: manipulation;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	width: auto;
	// display: inline-block;
	// width: auto;
	// height: auto;
	// padding: .75em 3em;
	// line-height: 1.2;
	// color: #fff;

	// border: 0;
	// box-shadow: none;
	// border-radius: 12325px;
	// font-weight: 800;
	// text-decoration: none;
	// text-transform: uppercase;
	transition: background .15s ease-out;
}

.atcb_button:hover {
	// background: #fff;
	background: var(--c-primary-hover);
}

@media only screen and (max-width: v.$b-small) {
	.atcb_button {
		font-size: 14px;
	}
}

.atcb_button.active {
	box-shadow: 1px 5px 15px 0 rgba(0, 0, 0, .5);
	margin: 7px auto 9px auto;
	padding: 12px 20px 13px 20px;
	z-index: 90;
}

.atcb_icon {
	height: 16px;
	display: inline-flex;
	margin-bottom: 4px;
	margin-right: 10px;
	vertical-align: middle;
}

.atcb_icon svg {
	height: 100%;
	fill: #fff;
	width: auto;
}

.atcb_list_item .atcb_icon svg {
	fill: var(--c-text);
}

.atcb_list {
	box-sizing: border-box;
	color: #333;
	display: block;
	margin: -10px auto 0 auto;
	max-width: 100%;
	position: absolute;
	padding: 0 3px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	width: 100%;
	z-index: 80;
}

.atcb_list_item {
	background: #fafafa;
	border: 1px solid #d2d2d2;
	border-top: 0;
	-webkit-box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, .3);
	box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, .3);
	box-sizing: border-box;
	cursor: pointer;
	font-size: 16px;
	left: 50%;
	position: relative;
	padding: 12px 18px;
	text-align: left;
	transform: translate(-50%);
	touch-action: manipulation;
	-webkit-tap-highlight-color: transparent;
}

.atcb_list_item:hover {
	background: #fff;
	-webkit-box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, .4);
	box-shadow: 1px 2px 10px 0 rgba(0, 0, 0, .4);
	color: #000;
}

@media only screen and (max-width: 575px) {
	.atcb_list_item {
		font-size: 14px;
	}
}

.atcb_list_item:first-child {
	padding-top: 25px;
}

.atcb_list_item:last-child {
	border-radius: 0 0 6px 6px;
}

.atcb_list_item .atcb_icon {
	margin-right: 8px;
}

.atcb_bgoverlay {
	background: rgba(20, 20, 20, .2);
	backdrop-filter: blur(2px);
	height: 100%;
	left: 0;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 70;
}
